import React, { useEffect, useState } from "react";
import ChatBox from "./ChatBox";
import '../styles/chat-box-iframe.css'
import '../styles/page.css'
import { useParams } from "react-router-dom";
import { getChatbotSettings } from "../lib/api/ApiChatbot";



export default function ChatBoxIframe({ defaultId }) {

    const [isLoaded, setIsLoaded] = useState(false)
    const [setting, setSetting] = useState({
        isDark: false,
        suggestedMsg: [],
        initialMsg: [],
        title: "",
        icon: "",
        autoInitialMsgAfter: 0,
        msgCoolDownTime: 1,
        maxMsgAllow: 5,
        isActive: true,
        isPowered: true


    })

    const { id } = useParams()

    useEffect(() => {

        if (defaultId) {
            loadData(defaultId)
        } else {
            loadData(id)
        }

        console.log(defaultId)

    }, [])

    async function loadData(id) {

        const res = await getChatbotSettings(id)
        if (res.data) {
            // console.log(res.data)


            const isDark = res.data.view[0].bot_theme == "light" ? false : true
            const suggestedMsg = res.data.view[0].suggested_messages.split('\n').filter((item) => item)
            const initialMsg = res.data.view[0].default_message.split('\n').filter((item) => item)
            const autoInitialMsgAfter = res.data.view[0].auto_showing_initial_msg_in_seconds
            const msgCoolDownTime = res.data.view[0].restiction_over_time_to_sent_msg
            const maxMsgAllow = res.data.view[0].limit_per_user_msgs
            const isPowered = res.data.view[0].remove_powered_by
            const isActive = true

            const da = {
                isDark: isDark,
                suggestedMsg: suggestedMsg,
                initialMsg: initialMsg,
                title: res.data.view[0].name,
                icon: res.data.view[0].bot_picture,
                autoInitialMsgAfter: autoInitialMsgAfter,
                msgCoolDownTime: msgCoolDownTime,
                maxMsgAllow: maxMsgAllow,
                isActive: isActive,
                isPowered: !isPowered

            }

            setSetting(da)

            setIsLoaded(true)

        } else {
            console.log(res)
        }



    }


    return <div className="chat" >
        {isLoaded &&
            <ChatBox chatbotId={defaultId ? defaultId : id} isActive={setting.isActive} isPowered={setting.isPowered} autoInitialMsgAfter={setting.autoInitialMsgAfter} msgCoolDownTime={setting.msgCoolDownTime} maxMsgAllow={setting.maxMsgAllow} icon={setting.icon} title={setting.title} isDark={setting.isDark} suggestedMsg={setting.suggestedMsg} initialMsg={setting.initialMsg} />}
    </div>
}