import React, { useEffect, useRef, useState } from "react";

import '../styles/chat-box.css'
import logo from '../assets/logo2.png'
import SlowTypeText from "./Widget/SlowTypeText";
import { getChat } from "../lib/api/ApiChatbot";
import toast from "react-hot-toast";


export default function ChatBox({ chatbotId = '', autoInitialMsgAfter = 3, msgCoolDownTime = 1, maxMsgAllow = 5, isActive = true, isPowered = true, isTest = false, title = "", icon = "", isSmall = false, suggestedMsg = [], initialMsg, isDark = true, userMsgColor }) {


    // type : user | bot | loading
    const [message, setMessage] = useState([])
    let botMsgCount = useRef([])
    let isCoolDown = useRef(false)

    function getCurrentTime() {
        return new Date().getTime()
    }

    function loadChatHistory() {
       
        const item2 = localStorage.getItem('urban_chat_count')
        if(item2){
            const arr = JSON.parse(item2)
            const oneDaysBeforeTime = getCurrentTime() - 1 * 24 * 60 * 60 * 1000    // - 1 days time
            const todayCount = arr.filter((time) => time > oneDaysBeforeTime)
            botMsgCount.current = todayCount
        }

        const item = localStorage.getItem('urban_chat_history')
        if (item) {
            const arr = JSON.parse(item)
            const fiveDaysBeforeTime = getCurrentTime() - 5 * 24 * 60 * 60 * 1000    // - 5 days time
            const filterChat = arr.filter((item) => item.time > fiveDaysBeforeTime)
            
            saveChatHistory(filterChat)
            return filterChat
        }

        return []
    }

    function saveChatHistory(chats) {

        if (isTest) return
        let json = JSON.stringify(chats)
        localStorage.setItem("urban_chat_history", json)


        let json2 = JSON.stringify(botMsgCount.current)
        localStorage.setItem('urban_chat_count', json2)
    }

    useEffect(() => {
        const history = loadChatHistory()
        if (!isTest) setMessage(history)
        const timer2 = setTimeout(scrollToBottom, 1000)

        if (history.length == 0) {
            const timer = setTimeout(showInitialMsg, autoInitialMsgAfter * 1000)
            return () => {
                clearTimeout(timer)
                clearTimeout(timer2)
            }
        }

        isCoolDown.current = true

        return () => {
            clearTimeout(timer2)
        }

    }, [])


    useEffect(() => {
        scrollToBottom()
    }, [message])

    function showInitialMsg() {
        console.log('msg added')
        setMessage(initialMsg.map((item) => { return { message: item, type: 'bot', time: getCurrentTime() } }))
        isCoolDown.current = true
    }



    //   used in test mode only
    // useEffect(() => {
    //     if (!initialMsg) return
    //     if (!isTest) return
    //     setMessage(initialMsg.map((item) => { return { message: item, type: 'bot', time: getCurrentTime() } }))
    // }, [initialMsg])


    const inputBox = useRef()
    const [isProgress, setIsProgress] = useState(false)


    function onClean() {
        if (!initialMsg) return
        saveChatHistory([])
        setMessage(initialMsg.map((item) => { return { message: item, type: 'bot', time: getCurrentTime() } }))
    }




    function sleep(ms) {
        return new Promise(resolve => setTimeout(resolve, ms));
    }

    async function onSubmit(event) {
        event.preventDefault()
        if (inputBox.current.value.trim() == '') return
        sendMsg(inputBox.current.value)
        inputBox.current.value = ""
    }




    // readChunks() reads from the provided reader and yields the results into an async iterable
    function readChunks(reader) {
        return {
            async*[Symbol.asyncIterator]() {
                let readResult = await reader.read();
                while (!readResult.done) {
                    yield readResult.value;
                    readResult = await reader.read();
                }
            },
        };
    }



    const scrollEle = useRef()
    const scrollToBottom = () => {
        scrollEle.current.scrollTop = scrollEle.current.scrollHeight;

    };


    async function sendMsg(text) {
        if (!isActive || !isCoolDown.current || isProgress) {
            return
        }

        if(maxMsgAllow <= botMsgCount.current.length){
            toast.error('Today limit reached')
            return
        }
        
        
        setIsProgress(true)
        isCoolDown.current = false
        setTimeout(() => isCoolDown.current = true, msgCoolDownTime * 1000)

        setMessage((old) => [...old, { message: text, type: 'user', time: getCurrentTime() }, { message: '', type: 'loading' }])

        const data = {
            chatbot_id: chatbotId,
            messages: [{ role: 'user', content: text }]
        }


        fetch('https://urbanchat.ai/api/chat-completion', {
            method: 'POST',
            headers: {
                'Content-Type': 'application/json',
            },
            body: JSON.stringify(data)
        })
            .then(async (response) => {
                if (!response.ok) {
                    throw new Error(`HTTP error! Status: ${response.status}`);
                }

                const time = getCurrentTime()

                setMessage((old) => {
                    const copy = old.slice();
                    copy.pop();
                    copy.push({ message: "", type: 'bot', time: time });
                    return copy;
                });

                const reader = response.body.getReader();
                const textDecoder = new TextDecoder('utf-8');
                for await (const chunk of readChunks(reader)) {
                    const resultString = textDecoder.decode(chunk);
                    // console.log(`received chunk of size ${resultString}`);
                    setMessage((old) => {
                        const copy = old.slice();
                        const oldStr = copy[copy.length - 1].message;
                        copy[copy.length - 1] = { message: oldStr + resultString, type: 'bot', time: time };
                        return copy;
                    });

                }


                setIsProgress(false)
                botMsgCount.current.push(getCurrentTime())
                setMessage((old) => {
                    saveChatHistory(old)
                    return old
                })

            })
            .catch((error) => {
                console.error('Error during fetch:', error);
                setIsProgress(false)
                // Handle the error here, for example, update the UI or log the error.
            });



    }


    return <div className={isDark ? "chat-box" : "chat-box light"}>
        <div className="header">
            <div>
                <img className="logo" src={icon ? icon : logo} />
                <span>{title ? title : "Urban Chat"}</span>
            </div>
            <button onClick={onClean} className="hover"><svg fill="none" viewBox="0 0 24 24" xmlns="http://www.w3.org/2000/svg"><path d="m14.712 2.289-.087-.078a1 1 0 0 0-1.327.078l-.078.087a.999.999 0 0 0 .078 1.326l1.299 1.297H8.999l-.24.004A6.997 6.997 0 0 0 2 11.993c0 1.445.438 2.788 1.189 3.899a.999.999 0 0 0 1.626-1.163l-.135-.218A4.997 4.997 0 0 1 9 6.998h5.595l-1.297 1.297-.078.087a.999.999 0 0 0 1.492 1.326l3.006-3.003.077-.087a.999.999 0 0 0-.078-1.326l-3.005-3.003Zm6.075 5.771A.999.999 0 0 0 19 8.677c0 .209.064.402.172.561a4.997 4.997 0 0 1-4.17 7.75H9.414l1.294-1.29.083-.096a1 1 0 0 0-.006-1.23l-.077-.088-.095-.084a1.001 1.001 0 0 0-1.232.006l-.088.078-3.005 3.003-.083.095a1 1 0 0 0 .006 1.231l.077.087 3.005 3.003.095.084a1 1 0 0 0 1.397-1.41l-.077-.087-1.304-1.303h5.596l.24-.003a6.997 6.997 0 0 0 5.546-10.927v.003Z" /></svg></button>
        </div>
        <div ref={scrollEle} className="box-content">

            {message.map((value, index) => {
                if (value.type == 'user') {
                    return <div key={index} className="chat primary">
                        <p>{value.message}</p>
                    </div>
                } else if (value.type == 'bot') {
                    return <div key={index} className="chat">
                        <p>{value.message}</p>
                        {/* <SlowTypeText text={value.message} /> */}
                    </div>
                } else {
                    return <div key={index} className="chat">
                        <div>
                            <div className="progress-load">
                                <div className="item"></div>
                                <div className="item"></div>
                                <div className="item"></div>
                            </div>
                        </div>
                    </div>
                }
            })}

        </div>
        <hr />

        <div className="chat-footer">

            <div className="default">
                {suggestedMsg.map((item, index) => <button key={index} className="hover" onClick={() => sendMsg(item)}>{item}</button>)}
            </div>

            <form onSubmit={onSubmit}>
                <input disabled={!isActive} ref={inputBox} name="msg" placeholder="Write whatever in your mind..." type="text" />
                <hr />
                <button type="submit" className="hover"><svg fill="none" viewBox="0 0 24 24" xmlns="http://www.w3.org/2000/svg"><path d="m12.815 12.197-7.532 1.256a.5.5 0 0 0-.386.318L2.3 20.728c-.248.64.421 1.25 1.035.943l18-9a.75.75 0 0 0 0-1.342l-18-9c-.614-.307-1.283.304-1.035.943l2.598 6.957a.5.5 0 0 0 .386.319l7.532 1.255a.2.2 0 0 1 0 .394Z" /></svg></button>

            </form>
            {!isPowered ? <p className="power"> </p> : <p className="power">Powered by <a target="_blank" href="https://urbanchat.ai">UrbanChat</a></p>}
        </div>
    </div>
}

